import React, {useState} from 'react';
import './App.css';
import {TaskType, TodoList} from "./components/todoLlist/TodoList";
import {v1} from "uuid";
import {AddItemForm} from "./components/todoLlist/AddItemForm";
import {
    AppBar,
    Box,
    Button,
    Container, createTheme, CssBaseline,
    Grid,
    IconButton,
    Paper,
    ThemeProvider,
    Toolbar,
    Typography
} from "@mui/material";
import MenuIcon from '@mui/icons-material/Menu';


export type filterValueType = 'all' | 'active' | 'completed'

type TodoListsType = {
    id: string
    title: string
    filter: filterValueType
}

type TaskStateType = {
    [key: string]: Array<TaskType>
}
const darkTheme = createTheme({
    palette: {
        mode: 'dark',
    },
});

function App() {

    const todolistId1 = v1()
    const todolistId2 = v1()

    let [todoLists, setTodoLists] = useState<Array<TodoListsType>>([
        {id: todolistId1, title: "What to buy", filter: "all"},
        {id: todolistId2, title: "What to learn", filter: "all"}
    ])


    let [todoListTasks, setTodoListTasks] = useState<TaskStateType>({
        [todolistId1]: [
            {id: v1(), title: 'bread', isDone: false},
            {id: v1(), title: 'egs', isDone: true},
            {id: v1(), title: 'milk', isDone: false},
            {id: v1(), title: 'orange', isDone: true}],
        [todolistId2]: [
            {id: v1(), title: 'bёread', isDone: false},
            {id: v1(), title: 'es', isDone: true},
            {id: v1(), title: 'miэlk', isDone: false},]
    });

    function removeTodoList(todoListId: string) {
        let filtreTodoLists = todoLists.filter(tl => tl.id !== todoListId)
        if (filtreTodoLists) {
            setTodoLists(filtreTodoLists)
            delete todoListTasks[todoListId]
            setTodoListTasks({...todoListTasks})

        }
    }

    function changeTodoListTitle(newTitle: string, todoListId: string) {
        let todoList = todoLists.find(tl => tl.id === todoListId)
        if (todoList) {
            todoList.title = newTitle
            setTodoLists([...todoLists])
        }

    }

    function removeTask(id: string, todoListId: string) {
        let taskObj = todoListTasks[todoListId]
        let filtredTasks = taskObj.filter(t => t.id !== id)
        todoListTasks[todoListId] = filtredTasks
        setTodoListTasks({...todoListTasks})
    }

    function addTask(title: string, todoListId: string) {
        let taskObj = todoListTasks[todoListId]
        let newTask = {id: v1(), title: title, isDone: false}
        let newTasks = [newTask, ...taskObj]
        todoListTasks[todoListId] = newTasks
        setTodoListTasks({...todoListTasks})

    }

    function addTodolist(title: string) {
        const newTodolist: TodoListsType = {
            id: v1(),
            title: title,
            filter: "all"
        }
        setTodoLists([...todoLists, newTodolist])
        setTodoListTasks({...todoListTasks, [newTodolist.id]: []})

    }

    function changeStatus(taskId: string, isDone: boolean, todoListId: string) {
        let taskObj = todoListTasks[todoListId]
        let task = taskObj.find(t => t.id === taskId)
        if (task) {
            task.isDone = isDone
        }

        setTodoListTasks({...todoListTasks})

    }

    function changeTaskTitle(taskId: string, newTitle: string, todoListId: string) {
        let taskObj = todoListTasks[todoListId]
        let task = taskObj.find(t => t.id === taskId)
        if (task) {
            task.title = newTitle
        }

        setTodoListTasks({...todoListTasks})

    }

    function changeFilter(value: filterValueType, todoListsId: string) {
        let todolist = todoLists.find(tl => tl.id === todoListsId)
        if (todolist) {
            todolist.filter = value
            setTodoLists([...todoLists])
        }


    }





    return (
        <>
            <ThemeProvider theme={darkTheme}>
                <CssBaseline/>
                <Box sx={{flexGrow: 1}}>
                    <AppBar position="static">
                        <Toolbar>
                            <IconButton
                                size="large"
                                edge="start"
                                color="inherit"
                                aria-label="menu"
                                sx={{mr: 2}}
                            >
                                <MenuIcon/>
                            </IconButton>
                            <Typography variant="h6" component="div" sx={{flexGrow: 1}}>
                                TodoList
                            </Typography>
                            <Button color="inherit">Login</Button>

                        </Toolbar>
                    </AppBar>
                </Box>
                <Container>
                    <Grid>
                        <Paper style={{padding: "10px", display: 'inline-block', margin: "10px 0px 20px 0px"}}
                               elevation={3}>
                            <h3 style={{padding: "0", margin: '0'}}>Add TodoList</h3>
                            <AddItemForm variant={'outlined'} addItem={addTodolist}/>
                        </Paper>
                    </Grid>
                    <Grid container spacing={5}>
                        {
                            todoLists.map((tl) => {

                                let filtredTasks = todoListTasks[tl.id]
                                if (tl.filter === 'completed') {
                                    filtredTasks = filtredTasks.filter(t => t.isDone === true)
                                }
                                if (tl.filter === 'active') {
                                    filtredTasks = filtredTasks.filter(t => t.isDone === false)
                                }

                                return <Grid item>
                                    <Paper style={{padding: "20px"}} elevation={3}>
                                        <TodoList
                                            changeTodoListTitle={changeTodoListTitle}
                                            onChangeTaskTitle={changeTaskTitle}
                                            removeTodoList={removeTodoList}
                                            key={tl.id}
                                            id={tl.id}
                                            addTask={addTask}
                                            removeTasks={removeTask}
                                            title={tl.title}
                                            tasks={filtredTasks}
                                            changeFilter={changeFilter}
                                            changeTaskStatus={changeStatus}
                                            filter={tl.filter}
                                        />
                                    </Paper>
                                </Grid>
                            })
                        }
                    </Grid>

                </Container>
            </ThemeProvider>
        </>);
}

export default App;
